import { Component, Vue } from "vue-property-decorator";
import firebaseAuthService from "@/services/FirebaseAuthService";
import MobileVerify from "@/components/otp/MobileVerify.vue";
import Orders from "@/components/Orders/Orders.vue";

import AddressPopUp from "@/components/AddressPopUp/AddressPopUp.vue";
import Address from "@/dto/Address";

@Component({
  components: {
    MobileVerify,
    AddressPopUp,
    Orders
  }
})
export default class Dashboard extends Vue {
  items = [
    { title: 'Dashboard', icon: 'mdi-view-dashboard', path: '/dashboard/home' },
    { title: 'Library', icon: 'mdi-library', path: '/dashboard/library' },
    { title: 'Orders', icon: 'mdi-account-box', path: '/dashboard/orders' },
    { title: 'Personal Info', icon: 'mdi-account', path: '/dashboard/details' },
    { title: 'My Tickets', icon: 'mdi-note-text', path: '/dashboard/tickets' },
  ];
  address: Address = new Address();
  get userDrawer() {
    return this.$store.getters.getUserDrwer;
  }
  set userDrawer(value: boolean) {
    this.$store.dispatch('setUserDrawer', value);
  }
  async logOut() {
    try {
      await firebaseAuthService.signOut(this.$route.name as string);
    } catch (error) {
      console.log(error)
    }
  }
}
